
import axios from 'axios';
import { mapGetters } from 'vuex';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Previewer,
ViewBox,
Popup,
},
filters: {
formatDuring: function(value) {
if (!value) {
return '无';
} else {
Date.prototype.toLocaleString = function() {
let months = this.getMonth() + 1;
if (months < 10) {
months = '0' + months;
}
let dates = this.getDate();
if (dates < 10) {
dates = '0' + dates;
}
let hours = this.getHours();
if (hours < 10) {
hours = '0' + hours;
}
let minutes = this.getMinutes();
if (minutes < 10) {
minutes = '0' + minutes;
}
return this.getFullYear() + '.' + months + '.' + dates + ' ' + hours + ':' + minutes;
};
return new Date(value).toLocaleString();
}
}
},
methods: {
cos(){
console.log('1111');
},
show(index) {
this.$refs.previewer.show(index);
},
reject(){
axios.get('pstudy/reject', {
params: {
userid: this.user.userid,
studyid: this.$route.params.studyid
}
})
.then(res => {
if (res.success) {
this.$vux.alert.show({ title: res.msg,onHide(){
history.back(-1);
}});
} else {
this.$vux.alert.show({ title: res.msg });
}
})
.catch(err => {
console.log(err);
});
},
pass() {
axios
.get('pstudy/pass', {
params: {
userid: this.user.userid,
studyid: this.$route.params.studyid
}
})
.then(res => {
if (res.success) {
this.$vux.alert.show({ title: res.msg,onHide(){
history.back(-1);
}});
} else {
this.$vux.alert.show({ title: res.msg });
}
})
.catch(err => {
console.log(err);
});
},
spread() {
this.spr = true;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
folding() {
this.spr = !this.spr;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
noSpread() {
this.noSpr = true;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
noFolding() {
this.noSpr = !this.noSpr;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
logIndexChange(arg) {
console.log(arg);
},
getDetail() {
this.$http
.get('pstudy/queryById?studyid=' + this.$route.params.studyid)
.then(res => {
this.content = res.data;
this.content.picture = [];

this.num = res.data.pictures.length;
this.content.pictures.forEach(it => {
console.log(it);
var obj = {};
obj.msrc = 'http://dj.dlbdata.cn/dangjian/picture/show?pictureId=' + it.pictureId;
obj.src = 'http://dj.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + it.pictureId;
this.content.picture.push(obj);
});
})
.catch(err => {
console.log(err);
});
},
getModule() {
this.$http
.get('pscoredetail/queryById?id=' + this.$route.params.moduleid)
.then(res => {
this.contents.title = res.data.projectName+'评分';
})
.catch(err => {
console.log(err);
});
},
getDetail() {
this.$http
.get('pstudy/queryById?studyid=' + this.$route.params.studyid)
.then(res => {
this.content = res.data;
this.content.picture = [];
this.num = res.data.pictures.length;
this.content.pictures.forEach(it => {
var obj = {};
obj.msrc = 'http://dj.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=' + it.pictureId;
obj.src = 'http://dj.dlbdata.cn/dangjian/picture/show?pictureId=' + it.pictureId;
this.content.picture.push(obj);
});
})
.catch(err => {
console.log(err);
});
},
dataPick(s) {
Date.prototype.toLocaleString = function() {
return this.getFullYear() + '年' + (this.getMonth() + 1) + '月' + this.getDate() + '日';
};
return new Date(s).toLocaleString();
},
showMenu(){
this.showPop = true;
},
know(){
this.showPop = false;
},
getmoduleid(){
this.moduleid=this.$route.params.moduleid

},
},

mounted() {
this.getDetail();
this.getModule();
this.getmoduleid();
},
computed: {
...mapGetters(['user'])
},
data() {
return {
moduleid:'',
contents:{title:''},
num: 0,
activeData: {},
picInfo: [],
list: [],
content: {},
spr: false,
noSpr: false,
nobtnPack: false,
nobtnAn: false,
btnPack: false,
btnAn: false,
peopleNum: null,
participants: null,
Noparticipants: null,
showPop:false,
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index];

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
}
};
},
};



